.button {
  @media screen and (min-width: 320px) {
    width: 280px;
    height: 50px;
    padding-top: 13px;
    padding-bottom: 13px;
    background-color: #24cca7;
    font-family: CirceRegular;
    font-size: 18px;
    line-height: 27px;
    text-align: center;
    letter-spacing: 0.1em;
    text-transform: uppercase;
    color: white;
    border: none;
    border-radius: 20px;
    cursor: pointer;
    transition: background-color 00ms;

    &:hover,
    &:focus {
      background-color: #21bd9b;
    }
  }

  @media screen and (min-width: 768px) {
    width: 300px;
  }
}
